import { defineComponent } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        title: {
            type: String,
            default: '',
            required: true
        },
        description: {
            type: String,
            default: '',
            required: true
        },
        tags: {
            type: Array,
            default: () => []
        },
        badges: {
            type: Array,
            default: () => []
        },
        todos: {
            type: Array,
            default: () => []
        },
        assignees: {
            type: Array,
            default: () => []
        },
        users: {
            type: Array,
            default: () => []
        }
    },
    setup(_, { emit }) {
        const onBadgeClick = (badge) => {
            emit('onBadgeClick', badge);
        };
        const onAssigneeClick = (assignee) => {
            emit('onAssigneeClick', assignee);
        };
        const onUserClick = (user) => {
            emit('onUserClick', user);
        };
        const onTodoChange = (event, todo) => {
            emit('onTodoChange', { event, todo });
        };
        const onTagClick = (tag) => {
            emit('onTagClick', tag);
        };
        return {
            onBadgeClick,
            onAssigneeClick,
            onUserClick,
            onTodoChange,
            onTagClick
        };
    }
});
